<template v-if="homeSwiper">
	<view>
	<swiper
	class="swiper" 
	:indicator-dots="indicatorDots" 
	:autoplay="autoplay" 
	:interval="interval"
	:duration="duration" 
	:circular="circular" 
	:indicator-active-color="indicatorActiveColor" 
	:style="{height:height+'px'}">
		<swiper-item v-for="(item,index) in swiperList" :key="index" class="swiper-item">
			<a :href="item.link">
				<img :src="item.image" alt="" mode="widthFix">
			</a>
		</swiper-item>
	</swiper>
	</view>
</template>

<template v-else>
	<view>
	<swiper
	class="swiper" 
	:indicator-dots="indicatorDots" 
	:autoplay="autoplay" 
	:interval="interval"
	:duration="duration" 
	:circular="circular" 
	:indicator-active-color="indicatorActiveColor" 
	:style="{height:height+'px'}">
		<swiper-item v-for="(item,index) in swiperList" :key="index" class="swiper-item">
			<a :href="item">
				<img :src="item" alt="" mode="widthFix">
			</a>
		</swiper-item>
	</swiper>
	</view>
</template>

<script>
	export default {
		name: 'Swiper',
		props: {
			swiperList: {
				type: Array,
				default () {
					return []
				}
			},
			homeSwiper:{
				type:Boolean,
				default:false
			},
			indicatorDots: {
				type: Boolean,
				default: true
			},
			autoplay: {
				type: Boolean,
				default: true
			},
			interval: {
				type: Number,
				default: 5000
			},
			duration: {
				type: Number,
				default: 500
			},
			circular: {
				type: Boolean,
				default: true
			},
			indicatorActiveColor: {
				type: String,
				default: 'white'
			},
			height:{
				type:Number,
				default:150
			}
		},
	}
</script>

<style scoped>
	.swiper img {
		width: 100%;
	}
</style>
